import React from 'react'
import styled from 'styled-components'
import Item from 'Components/Item/Item'
import GetDataFromLocalStorage from 'utils/GetDataFromLocalStorage'
import MoveAfterVisit from 'utils/MoveAfterVisit'
import {
  FETCH_ERROR_MESSAGE,
  ALERT_NOT_INTERESTING_PRODUCT,
  WATCHED,
} from 'constant'
import { BASE_URL } from 'config'

class List extends React.Component {
  constructor(props) {
    super(props)
    this.state = { products: [] }
    this.handleClick = this.handleClick.bind(this)
  }

  componentDidMount() {
    fetch(BASE_URL)
      .then((response) => response.json())
      .then((response) => {
        this.setState({ products: response })
      })
      .catch(() => {
        console.log(FETCH_ERROR_MESSAGE)
      })
  }

  handleClick(product) {
    let data = []
    data = GetDataFromLocalStorage(WATCHED) || []
    if (data) {
      // ÏÉÅÌíà Ï°∞Ìöå Ïù¥Î†•Ïù¥ Ï°¥Ïû¨ÌïúÎã§Î©¥
      for (let i = 0; i < data.length; i++) {
        if (data[i].index === product.index) {
          // Ïù¥ÎØ∏ Ìïú Î≤à Ï°∞ÌöåÌïú Ïù¥Î†•Ïù¥ Ï°¥Ïû¨ÌïòÎäî ÏÉÅÌíàÏù¥ÎùºÎ©¥
          if (data[i].interest === false) {
            // 'Í¥ÄÏã¨ ÏóÜÏùå' ÏÉÅÌíàÏù¥ÎùºÎ©¥ alert
            alert(ALERT_NOT_INTERESTING_PRODUCT)
            return
          } else {
            // 'Í¥ÄÏã¨ ÏóÜÏùå' ÏÉÅÌíàÏù¥ ÏïÑÎãàÎùºÎ©¥ Ï°∞Ìöå ÏãúÍ∞Ñ Î∞è ÎÇ†Ïßú Í∞±Ïã†
            // ÏÉÅÏÑ∏ ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô
            data[i].date = product.date
            MoveAfterVisit(data, `/product/${product.index}`, this.props)
            return
          }
        }
      }
      // Ï°∞Ìöå Ïù¥Î†•Ïù¥ ÏóÜÎäî ÏÉÅÌíàÏù¥ÎùºÎ©¥
      // Ìï¥Îãπ ÏÉÅÌíà Ï†ïÎ≥¥ LocalStorageÏóê Ï†ÄÏû•(Ï°∞Ìöå Ï≤òÎ¶¨) Î∞è ÏÉÅÏÑ∏ ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô
      data.push(product)
      MoveAfterVisit(data, `/product/${product.index}`, this.props)
      return
    }
    // LocalStorage ÎπÑÏñ¥ÏûàÎã§Î©¥
    // ÌÅ¥Î¶≠Ìïú ÏÉÅÌíà Ï†ïÎ≥¥ LocalStorageÏóê Ï†ÄÏû•(Ï°∞Ìöå Ï≤òÎ¶¨) Î∞è ÏÉÅÏÑ∏ ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô (index Î∞è ÎÇ†Ïßú Ï†ïÎ≥¥ Ï∂îÍ∞ÄÌï¥ÏÑú)
    data.push(product)
    MoveAfterVisit(data, `/product/${product.index}`, this.props)
  }

  render() {
    return (
      <>
        <PageTitle>Ï†ÑÏ≤¥ ÏÉÅÌíà Î™©Î°ù</PageTitle>
        <ProductsList>
          {this.state.products.map((product, index) => {
            return (
              <Product
                key={index}
                onClick={() => {
                  const date = new Date()
                  this.handleClick({
                    index: index,
                    title: product.title,
                    brand: product.brand,
                    price: product.price,
                    date: date,
                    interest: true,
                  })
                }}
              >
                <Item
                  item={{
                    title: product.title,
                    brand: product.brand,
                    price: product.price,
                  }}
                />
              </Product>
            )
          })}
        </ProductsList>
      </>
    )
  }
}

const PageTitle = styled.div`
  margin-top: 30px;
  padding-left: 68px;
  font-size: 36px;
  font-weight: bold;
  width: 100%;
`

const ProductsList = styled.div`
  width: 630px;
  margin: 0 auto;
`

const Product = styled.div`
  cursor: pointer;
`

export default List
